body{
  margin: 0;
  padding: 0;
}
#appbody{
  margin: 0;
  padding: 0;
  display: flex;
}
aside{
  background-color: #262727;
  height: 795px;
  flex: 1;
}
.asideul{
  margin: 0px;
  padding: 10px 30px;
  list-style: none;
}
.asidelist{
  padding: 10px 20px;
  margin: 10px auto;
  border-radius: 5px;
  background-color: #3a3939;
  color: #fff;
}
#left_section{
  margin: 0;
  flex: 5;
}
nav{
  margin: 0;
  background-color:#2d2d2d;
  box-shadow: 9px 2px 8px 2px #737272;
}
nav .navul{
  margin: 0;
  display: flex;
  padding: 20px 0px;
  list-style: none;
  justify-content:end;
  align-items: center;
}
nav .navul .navlist{
  padding: 0px 20px;
  color: #fff;
}
#dashboard{
  padding: 10px;
  background-color: #3a3939;
  height: 700px;
}
#dashboard p{
  color: #fff;
  background-color: #4f4e4e;
  padding: 20px 30px;
  border-radius: 5px;
  width: 80%;
  margin: 20px auto;
}